
import http from './http'


export function createExerciseSetApi(data) {
    return http.post("/api/exerciseSets",data)
}

export function fetchExerciseSetsApi(options) {
    return http.get("/api/exerciseSets", options)
}

export function deleteExerciseSetApi(sid) {
    return http.delete("/api/exerciseSets/"+ sid)
}


export function fetchExerciseSetDetailApi(id) {
    return http.get("/api/exerciseSets/" + id)
}

export function fetchExercisesApi(options) {
    return http.get("/api/exercises", options)
}

export function createExerciseApi(exercise) {
    return http.post("/api/exercises", exercise)
}

export function deleteExerciseApi(id) {
    return http.delete(`/api/exercises/${id}`)
}

export function updateExerciseSetApi(id, data) {
    return http.put(`/api/exerciseSets/${id}`, data)
}

export function fetchExerciseMetaApi(id) {
    return http.get(`/api/exercises/${id}/meta`)
}

export function updateExerciseApi(id, exercise) {
    return http.put('/api/exercises/' + id, exercise)
}


export function startExerciseApi(id) {
    return http.get(`/api/exercises/${id}/start`)
}

export function restartExerciseApi(id) {
    return http.post(`/api/exercises/${id}/restart`)
}

export function submitCodeApi(id, data) {
    return http.post(`/api/exercises/${id}/submit`,data)
}

export function pubExerciseApi(id) {
    return http.post(`/api/exercises/${id}/publish`)
}

export function getExerciseConfigApi(id) {
    return http.get(`/api/exercises/${id}/config`)
}

export function updateExerciseConfigApi(id, data) {
    return http.put(`/api/exercises/${id}/config`, data)
}

export function publishExerciseSetApi(id) {
    return http.post(`/api/exerciseSets/${id}/publish`)
}